<template>
    <div>
        <el-dialog title="" :visible.sync="boxShow.show" width="100%">
            <p></p>
                <el-table :data="gridData" height="290" >
                    <el-table-column property="date" label="" width="150"></el-table-column>
                    <el-table-column property="name" label="" width="200"></el-table-column>
                    <el-table-column property="address" label=""></el-table-column>
                    <el-table-column property="value" label=""></el-table-column>
                    <el-table-column property="link" label="">
                        <template slot-scope="scope">
                        <el-button type="primary" size="small" icon="el-icon-edit-outline" 
                            @click="handleOpen(scope.$index, scope.row)">
                            
                        </el-button>
                        </template>
                    </el-table-column>
                </el-table>
        </el-dialog>
    </div>

</template>
<script>
export default {
    name: 'Baibao',
    props: {
        boxShow: Object
    },
    data() {
        return {
            fits: ['fill', 'contain', 'cover', 'none', 'scale-down', 'fill', 'contain', 'cover', 'none', 'scale-down'],
            url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
            gridData: [
            {
                date: '2010-05-02',
                name: '',
                address: '',
                value: '5400998',
                link: '/sixlog'
            },
            {
                date: '2020-05-02',
                name: '',
                address: '',
                value: '900'
            }, {
                date: '2019-05-04',
                name: '',
                address: '',
                value: '3990'
            }, {
                date: '2021-07-01',
                name: '',
                address: ' 1518 ',
                value: '1990'
            }, {
                date: '2019-05-13',
                name: '',
                address: '',
                value: '5'
            }, {
                date: '2021-08-03',
                name: '',
                address: '',
                value: "885"
            },
            {
                date: '2012-05-13',
                name: '',
                address: '',
                value: '9999999'
            }
            ],
        }
    },
    methods: {
        handleOpen(index, row) {
            console.log("row has link? :", row.link)
            if (row.link) {
                console.log("goes to sixlog now")
                this.$router.push({path: '/sixlog'})
            } else {
                // dont have extra link for this treasure
                console.log("no extra link")
            }
        }
    }
}
</script>



