<template>
  <div>
    <el-dialog title="" :visible.sync="dialogAdd.show" width="100%">
      <el-form
        :model="formDate"
        ref="formdong"
        label-width="100px"
        :rules="formrules"
      >
        <el-form-item label="" prop="date">
          <el-date-picker
            v-model="formDate.date"
            type="date"
            placeholder=""
          ></el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="evaluate">
          <el-select v-model="formDate.evaluate" placeholder="">
            <el-option
              v-for="item in evaluates"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
              <span style="float: left">{{ item.label }}</span>
              <img
                :src="item.value > 100 ? good_one : bad_one"
                style="width:20px; height:20px"
              />
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=" ">
          <span>{{ formDate.evaluate > 100 ? "" : "" }}</span>
          <img
            :src="formDate.evaluate > 100 ? good_one : bad_one"
            style="width: 30px; height: 30px"
          />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAdd.show = false"> </el-button>
        <el-button type="primary" @click="dialogFormAdd('formdong')"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { addNewFly } from '@/api/user'
export default {
  name: "AddFly",
  props: {
    dialogAdd: Object,
    owner: ""
  },
  data() {
    return {
      formDate: {
        date: new Date(),
        evaluate: "",
        type: ""
      },
      evaluates: [
        {
          value: "99",
          label: ""
        },
        {
          value: "98",
          label: ""
        },
        {
          value: "97",
          label: ""
        },
        {
          value: "101",
          label: ""
        },
        {
          value: "102",
          label: ""
        },
        {
          value: "103",
          label: ""
        },
        {
          value: "104",
          label: ""
        },
        {
          value: "105",
          label: ""
        },
        {
          value: "106",
          label: ""
        },
        {
          value: "107",
          label: "10"
        },
        {
          value: "108",
          label: ""
        },
        {
          value: "109",
          label: ""
        },
        {
          value: "110",
          label: ""
        },
        {
          value: "111",
          label: ""
        },
        {
          value: "112",
          label: ""
        },
        {
          value: "113",
          label: ""
        },
        {
          value: "114",
          label: ""
        },
        {
          value: "115",
          label: "3"
        },
        {
          value: "116",
          label: ""
        },
        {
          value: "117",
          label: ""
        },
        {
          value: "118",
          label: ""
        },
        {
          value: "119",
          label: ""
        },
        {
          value: "120",
          label: ""
        }
      ],
      good_one:
        "https://picgorepo.oss-cn-beijing.aliyuncs.com//2022-11-29-11-34-37README.png",
      bad_one:
        "https://picgorepo.oss-cn-beijing.aliyuncs.com//2022-11-29-11-34-29README.png",
      types: [
        {
          value:
            "https://picgorepo.oss-cn-beijing.aliyuncs.com//2022-11-29-11-34-37README.png",
          label: ""
        },
        {
          value:
            "https://picgorepo.oss-cn-beijing.aliyuncs.com//2022-11-29-11-34-29README.png",
          label: ""
        },
        {
          value:
            "https://picgorepo.oss-cn-beijing.aliyuncs.com/img_repo_2021-12-13-12-59-13.png",
          label: ""
        }
      ],
      formrules: {
        date: [
          {
            required: true,
            type: "date",
            message: "",
            trigger: "blur"
          }
        ],
        evaluate: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    dialogFormAdd(formdong) {
      this.$refs[formdong].validate(valid => {
        if (valid) {
          var _eva = null;
          for (var item of this.evaluates) {
            if (item.value == this.formDate.evaluate) {
              _eva = item.label;
            }
          }
          var good_name_list = [
            "",
            "",
            "Canada",
            ""
          ];
          var bad_name_list = [
            "",
            "",
            "",
            ""
          ];
          var gen_lmt = good_name_list.length;
          var _idx = Math.floor(Math.random() * gen_lmt);
          var _name =
            this.formDate.evaluate > 100
              ? good_name_list[_idx]
              : bad_name_list[_idx];
          var myData = {
            date: this.formDate.date,
            name: _name,
            evaluate: _eva,
            image: this.formDate.evaluate > 100 ? this.good_one : this.bad_one
          };
          
          addNewFly(this.owner, myData).then(res => {
            this.$message({
              type: "success",
              message: ""
            });
            this.dialogAdd.show = false;
            this.$emit("update");
          });
          this.formDate = {};
        } else {
          console.log("error when submit: not valid input in form");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped></style>



