<template>
    <div>
      <el-tree :data="people" :props="defaultProps" 
      @node-click="handleNodeClick"></el-tree>

    </div>
</template>

<script>
export default { 
  components: {},
  props: {},
  data() {
    return {
      people: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  //data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    getPeople() {
      this.$axios({
        url: "https://picgorepo.oss-cn-beijing.aliyuncs.com/family_data/jiapu.json",
        method: "get"
      })
        .then(resp => {
          console.log("resp:::", resp.data.children)
          this.people = resp.data.children
        })
        .catch(() => {});
    },
    handleNodeClick(data) {
      console.log(data)
    }
  },
  //-this
  created() {
    this.getPeople();
  },
  //-DOM
  mounted() {},
  beforeCreate() {}, //-
  beforeMount() {}, //-
  beforeUpdate() {}, //-
  updated() {}, //-
  beforeDestroy() {}, //-
  destroyed() {}, //-
  activated() {} //keep-alive
};
</script>
<style>
</style>


